<template name="components">
	<view class="page">
		<cu-custom bgColor="bg-black" :isBack="true"><block slot="content">人生答案之书</block></cu-custom>
		<view class="answer-box">
			<text>{{curAnswer}}</text>
		</view>
		<view class="btn-boxs">
			<navigator url="../entry/home" open-type="navigateBack">
				<image src="../../static/answer-book-again.png" mode="widthFix"></image>
			</navigator>
		</view>
	</view>
</template>

<script>
	import list from '../../static/answerList.js'
	export default {
		data() {
			return {
				curAnswer:''
			};
		},
		
		created() {
			let answerList = list.default
			this.curAnswer = answerList[Math.round(Math.random() * (answerList.length))].zh;
		},
		methods: {

		}
	}
</script>

<style lang="less" scoped>
	.page {
		height: 100vh;
		padding-bottom: 150rpx;
		background: #fff;
		
		.answer-box{
			position: absolute;
			top: 30%;
			width: 100%;
			text-align: center;
			font-size: 24px;
		}
		
		.btn-boxs{
			position: absolute;
			bottom: 5% !important;
			width: 50%;
			left: 25%;
		}
	}
</style>
